#home {
  width: 100%;
  height: 100vh;
  background-color: #f9f9fc;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

#home .main-pic {
  width: 30%;
  border: 9px solid #e2e2e2a6;
  border-radius: 9999px;
  transition: all .5s;
  overflow: hidden;
}

#home .main-pic img {
  width: 100%;
  height: 100%;
  transition: all .3s;
}

#home .main-pic img:hover {
  transform: scale(1.1);
}

@media (min-width: 400px) {
  #home .main-pic {
    width: 29%;
  }
}

@media (min-width: 960px) {
  #home .main-pic {
    width: 20%;
  }
}

#home h2 {
  color: #1a111a;
  margin-top: 1em;
  font-size: 2.1875rem;
  font-weight: 700;
}

#home .content {
  width: 100%;
  place-items: center;
  display: grid;
}

#home .content h3 {
  max-width: 85%;
  margin-top: .844em;
  font-size: 1.3125rem;
  font-weight: 400;
  line-height: 1.38em;
}

@media (min-width: 400px) {
  #home .content h3 {
    max-width: 85%;
  }
}

@media (min-width: 960px) {
  #home .content h3 {
    max-width: 65%;
  }
}

@media (min-width: 1280px) {
  #home .content h3 {
    max-width: 50%;
  }
}

#home .content nav {
  margin-top: 3em;
}

#home .content nav ul {
  gap: .455em;
  list-style: none;
  display: flex;
}

#home .content nav ul li {
  margin-bottom: 1.9em;
  padding: 0 1.2em;
}

#home .content nav ul li a img {
  width: 1.2em;
  height: 1.2em;
  transition: all .2s;
}

#home .content nav ul li a img:hover {
  transform: scale(1.4);
}

#home .content nav ul li:nth-child(-n+3) {
  border-right: 1px solid #7e7e7e4f;
}

#home .content nav ul li:nth-child(4) {
  transform: scale(1.4);
}

#home .content .download-cv {
  margin-top: .7m;
  color: #1a111a;
  z-index: 999;
  border: 1px solid #7e7e7e;
  border-radius: 9999px;
  padding: .6em 2.4em;
  font-size: 1.3125rem;
  font-weight: 400;
  line-height: 1.38em;
  text-decoration: none;
  transition: all .3s;
}

#home .content .download-cv:hover {
  color: #aaa;
  background-color: #1a111a;
  transform: translate(0%, -10%);
  box-shadow: 0 1px 4px #00000029;
}

@media (min-width: 400px) {
  #home .content .download-cv {
    padding: .3em 1.7em;
    font-size: 1.25rem;
    font-weight: 300;
    line-height: 1.38em;
  }
}

@media (min-width: 1440px) {
  #home .content .download-cv {
    padding: .6em 2.4em;
  }
}

